<template>
	<div class="main">
		<div>
			<div class="content">
				<div class="Genericpadding">
					<!-- 幻燈 -->
					<div class="indexconta">
						<el-carousel :interval="5000" arrow="always">
							<el-carousel-item v-for="item in 4" :key="item">
								<h3 text="2xl" justify="center">{{ item }}</h3>
							</el-carousel-item>
						</el-carousel>
					</div>
				</div>

				<el-carousel class="Genericpadding" height="20px" direction="vertical" indicator-position="none">
					<el-carousel-item v-if="arclist" v-for="(item, index) in arclist" :key="index">
						<div class="articlebt">
							<div class="Horizontalspacing minxgap">
								<div class="iconfont icon-gonggao center Green"></div>
								<div class="">公告</div>
								<div class="listtruncate-text turngreen"> {{ item.title }}</div>
							</div>
							<div class="Horizontalspacing interval minfont grey center">
								<div class="Horizontalspacing minxgap"
									><span>日期:</span><span>{{ item.Creationtime }}</span></div
								>
								<div class="Horizontalspacing minxgap"
									><span>閲讀:</span><span>{{ item.PageView }}</span></div
								>
							</div>
						</div>
					</el-carousel-item>
				</el-carousel>
				<div class="Genericpadding Intervalvertical">
					<!-- 放置頂課程  直播和錄播 -->
					<div>熱門課程</div>
					<div class="Horizontalspacing space-around">
						<div v-for="(item, index) in coursttabe" :key="index" class="TopRectangle">
							<router-link :to="/course/ + item.alias + '/' + item.id">
								<el-image class="TopPicture" :src="item.thumbnail" lazy>
									<template #error>
										<div class="image-slots iconfont icon-jiazaitupian"></div>
									</template>
								</el-image>

								<div class="Toppositioning Horizontalspacing minixgap">
									<span class="Green">[{{ item.pidname }}]</span>
									<span class="couretitle">{{ item.title }}</span>
								</div>
							</router-link>
						</div>
					</div>
				</div>
				<div class="ThinLineLayout">
					<div class="Genericpadding">
						<div class="QArectangle">
							<div class="Horizontalspacing interval">
								<div class="qzsolld" :class="{ qzsolldav: tesorting == 0 }" @click=";(tesorting = 0), (tosort = 'time'), tosortto()">全部</div>
								<div class="qzsolld" :class="{ qzsolldav: tesorting == 1 }" @click=";(tesorting = 1), (tosort = 'time'), (count = 5), tosortto()">按照時間</div>
								<div class="qzsolld" :class="{ qzsolldav: tesorting == 2 }" @click=";(tesorting = 2), (tosort = 'popularity'), (count = 5), tosortto()">按照人氣</div>
							</div>
						</div>
					</div>
					<ul class="ThinLineLayout">
						<!-- 文章1 -->
						<router-link v-if="arclist" v-for="(item, index) in arclist.slice(0, Math.min(count, arclist.length))" :key="index" :to="'/article/' + item.alias + '/' + item.id">
							<li class="Genericpadding Horizontalspacing interval navwidth">
								<div>
									<el-image class="PostThumbnails" :src="item.thumbnail ? item.thumbnail : ''" lazy>
										<template #error>
											<div class="image-slot iconfont icon-jiazaitupian"></div>
										</template>
									</el-image>
								</div>
								<div class="ArticleList">
									<div class="Horizontalspacing minxgap ArticleTitlep">
										<div class="smallsquare bold center"><span class="center">热门</span></div>
										<span class="turngreen Widthlimit">{{ item.title }}</span>
									</div>
									<span class="minfont grey Widthlimits">{{ item.description }}</span>
									<div class="articlebt minfont">
										<div class="Horizontalspacing interval">
											<div class="Horizontalspacing minixgap">
												<el-avatar class="pimgmins" :src="item.user.Avatar" :size="60">
													<div class="image-slot iconfont icon-setting-role"></div>
												</el-avatar>

												<span v-if="item.user.nickname" class="center">{{ item.user.nickname }}</span>
												<span v-else class="center">{{ item.user.username }}</span>
											</div>
											<div class="grey center">{{ item.pidname }}</div>
										</div>
										<div class="Horizontalspacing interval grey">
											<div class="Horizontalspacing minixgap">
												<span class="center">日期:</span>
												<span class="center">{{ item.Creationtime }}</span>
											</div>
											<div class="Horizontalspacing minixgap">
												<span class="center">点赞:</span>
												<span class="center">{{ item.Like.length }}</span>
											</div>
											<div class="Horizontalspacing minixgap">
												<span class="center">浏览:</span>
												<span class="center">{{ item.PageView }}</span>
											</div>
										</div>
									</div>
								</div>
							</li>
						</router-link>
						<!-- 文章2 -->
						<!-- 文章骨架 -->
						<div v-else>
							<el-skeleton v-for="index in 10" :key="index" class="Genericpadding Horizontalspacing interval navwidth" animated>
								<template #template>
									<div>
										<el-skeleton-item variant="image" style="width: 200px; height: 150px" />
									</div>
									<div class="ArticleList">
										<div>
											<el-skeleton :rows="3" style="width: 597px" animated />
										</div>
										<div class="articlebt minfont">
											<el-skeleton-item variant="h3" style="width: 154px" />
											<el-skeleton-item variant="h3" style="width: 319px" />
										</div>
									</div>
								</template>
							</el-skeleton>
						</div>

						<el-skeleton v-if="condition" v-for="index in 2" :key="index" class="Genericpadding Horizontalspacing interval navwidth" animated>
							<template #template>
								<div>
									<el-skeleton-item variant="image" style="width: 200px; height: 150px" />
								</div>
								<div class="ArticleList">
									<div>
										<el-skeleton :rows="3" style="width: 597px" animated />
									</div>
									<div class="articlebt minfont">
										<el-skeleton-item variant="h3" style="width: 154px" />
										<el-skeleton-item variant="h3" style="width: 319px" />
									</div>
								</div>
							</template>
						</el-skeleton>
					</ul>
					<div class="Genericpadding" @click=";(count += 2), More()">
						<div class="abstracts">
							<p class="center">查看更多</p>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div> <left v-my-directive :data-title="title" :data-description="description"></left> </div>
	</div>
</template>
<script>
import left from './../components/left'
import { dataRequest as api } from '@/api/api'
export default {
	name: 'name',
	components: {
		// 组件
		left,
	},
	data() {
		return {
			//返回值
			coursttabe: [],
			title: '',
			description: '我们提供专业的区块链培训和开发工具服务，学习区块链技术，获取区块链工具源码，欢迎加入区块链开发社区。',
			arclist: '',
			tesorting: 0,
			count: 10,
			qzsolldav: 'qzsolldav',
			tmime: true,
			page: 6,
			tosort: 'time',
			offset: 'DESC',
			moreoffset: 'DESC',
			condition: false,
		}
	},
	methods: {
		// 获取课程
		async course() {
			let array = await api(['post=Coursepagination', { Stickie: '1' }])
			console.log(array)
			if (array.code == 200) {
				this.coursttabe = array.data
			}
		},
		// 函数
		// 获取文章 1 第二页就是10个
		//順序怎麽切換

		async tosortto() {
			let array = await api(['post=getarticlepage', { perPage: this.count, page: 1, offset: this.offset, tosort: this.tosort, thumbnail: 'hasValue' }])
			this.moreoffset = this.offset
			this.offset = this.offset === 'DESC' ? 'ASC' : 'DESC'
			if (array.code == 200) {
				array = array.data
				this.arclist = array
				this.setvivws()
			}
		},
		async getdata() {
			// 正序 還是順序
			// 根據哪個表排序
			let array = await api(['post=getarticlepage', { perPage: this.count, page: 1, offset: this.offset, tosort: this.tosort, thumbnail: 'hasValue' }])

			if (array.code == 200) {
				array = array.data
				this.arclist = array
				this.setvivws()
			}
		},
		// 獲取後面的
		async More() {
			this.condition = true
			let array = await api(['post=getarticlepage', { perPage: 2, page: this.page, offset: this.moreoffset, tosort: this.tosort, thumbnail: 'hasValue' }])
			if (array.code == 200) {
				array = array.data
				this.condition = false
				this.arclist.push(...array)
				this.page += 1
				this.setvivw()
			}
		},
		// 新增浏览量
		setvivws() {
			let arr = []
			for (let i = 0; i < this.count && i < this.arclist.length; i++) {
				arr.push(this.arclist[i].id)
			}
			api(['post=acbrowsing', arr])
		},
		// 新增浏览量
		setvivw() {
			let arr = []
			for (let i = this.count - 2; i < this.count && i < this.arclist.length; i++) {
				arr.push(this.arclist[i].id)
			}
			api(['post=acbrowsing', arr])
		},
	},
	mounted() {
		// 生命周期:
		this.getdata()
		this.course()
	},
	updated() {},
}
</script>
<style scoped>
.el-carousel__item h3 {
	color: #475669;
	background-color: #99a9bf;
	opacity: 0.75;
	line-height: 300px;
	margin: 0;
	text-align: center;
}

.demonstration {
	display: block;
	color: var(--el-text-color-secondary);
	font-size: 14px;
	margin-bottom: 20px;
}

.image-slot {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 200px;
	height: 150px;
	background: var(--el-fill-color-light);
	color: var(--el-text-color-secondary);
	font-size: 30px;
}
.image-slots {
	display: flex;
	justify-content: center;
	align-items: center;
	width: 260px;
	height: 260px;
	background: var(--el-fill-color-light);
	color: var(--el-text-color-secondary);
	font-size: 30px;
}
.image-slot {
	font-size: 30px;
}
.pimgmins .image-slot {
	font-size: 14px;
}
.qzsolldav {
	color: var(--primary-color);
}

.Widthlimit {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 2; /* 设置最大显示行数 */
	overflow: hidden;
	text-overflow: ellipsis;
	width: 560px; /* 设置容器宽度 */
	white-space: normal; /* 允许换行 */
}
.Widthlimits {
	display: -webkit-box;
	-webkit-box-orient: vertical;
	-webkit-line-clamp: 3; /* 设置最大显示行数 */
	overflow: hidden;
	text-overflow: ellipsis;
	width: 597px; /* 设置容器宽度 */
	white-space: normal; /* 允许换行 */
}
.listtruncate-text {
	white-space: nowrap; /* 防止换行 */
	overflow: hidden; /* 隐藏超出部分 */
	text-overflow: ellipsis; /* 添加省略号 */
	width: 460px; /* 设置容器宽度 */
}
.couretitle {
	white-space: nowrap; /* 防止换行 */
	overflow: hidden; /* 隐藏超出部分 */
	text-overflow: ellipsis; /* 添加省略号 */
	width: 175px; /* 设置容器宽度 */
}

.navwidth {
	width: 860px;
}
</style>
